
import {
    getLocalStore,
    setLocalStore
} from './../config/global'

export default {
    // 1.
    syncuserInfo({
        commit,
    }, userInfo) {
        commit(USER_INFO, {
            userInfo
        });
    },
    // 2.Token 
    autoLogin({
        commit
    }) {
        // 2.1 
        let userInfo = getLocalStore('userInfo');
        if (userInfo) {
            commit(USER_INFO, {
                userInfo
            });
        }
    }
}